<template>
  <div>
    <tooltip :label="this.ip" placement="right" size="middle" :always="true" :rounded="true">
      <h1 class="title" >{{this.hostname}}</h1>
    </tooltip>
    <div class="tile is-ancestor">
      <div class="tile is-parent is-6">
        <article class="tile is-child box">
          <h1 class="title">WAF控制</h1>
          <div class="block styles-box">
            <p>
              <vb-switch :checked="waf" v-model="waf"></vb-switch><span>{{wafupdate}}</span>
            </p>
            <p>
              <vb-switch :checked="finger" v-model="finger"></vb-switch><span>{{fingerupdate}}</span>
            </p>
          </div>
        </article>
      </div>

      <div class="tile is-parent is-6">
        <article class="tile is-child box">
          <h1 class="title">主机控制</h1>
          <div class="block">
            <p>
              <vb-switch :checked="app" v-model="app"></vb-switch><span>{{appupdate}}</span>
            </p>
          </div>
        </article>
      </div>
    </div>

    <div class="tile is-ancestor">
      <div class="tile is-parent is-8">
        <article class="tile is-child box">
          <h1 class="title">节点信息</h1>
          <div class="block">
            <div class="">
              下载日志：<a :href="log()" target="_blank">{{this.hostname}}.log</a>
            </div>
            <hr>
            <table class="table is-striped">
              <thead>
                <tr>
                  <th>集群节点</th>
                  <th>状态</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>115.29.79.91</td>
                  <td>知识共享中</td>
                </tr>
                <tr>
                  <td>120.78.156.12</td>
                  <td>知识共享中</td>
                </tr>
                <tr>
                  <td>120.79.76.18</td>
                  <td>知识共享中</td>
                </tr>
              </tbody>
            </table>
          </div>
        </article>
      </div>

      <div class="tile is-parent is-4">
        <article class="tile is-child box">
          <h4 class="title">统计</h4>
          <p>正常请求：{{this.data[0]}} | 攻击访问：{{this.data[1]}} | 疑似0Day：{{this.data[2]}}</p>
          <chart :type="'doughnut'" :data="image2" :options="options"></chart>
        </article>
      </div>
    </div>

    <div class="tile is-ancestor">
      <div class="tile is-parent">
        <div class="tile is-child box">
          <h1 class="title">攻击数据详情</h1>
          <tabs layout="left" :only-fade="false">
            <tab-pane label="XSS攻击">
                <table class="table is-striped">
                  <thead>
                    <tr>
                      <th>Payload</th>
                      <th>来源</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="item in xss">
                      <td>{{ item.sre }}</td>
                      <td>{{ item.des }}</td>
                    </tr>
                  </tbody>
                </table>
            </tab-pane>
            <tab-pane label="SQL注入攻击">
              <table class="table is-striped">
                <thead>
                  <tr>
                    <th>Payload</th>
                    <th>来源</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="item in sql">
                    <td>{{ item.sre }}</td>
                    <td>{{ item.des }}</td>
                  </tr>
                </tbody>
              </table>
            </tab-pane>
            <tab-pane label="未知攻击">
              <table class="table is-striped">
                <thead>
                  <tr>
                    <th>Payload</th>
                    <th>来源</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="item in unknow">
                    <td>{{ item.sre }}</td>
                    <td>{{ item.des }}</td>
                  </tr>
                </tbody>
              </table>
            </tab-pane>
          </tabs>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// do not use below code, because `Switch` is svg tag.
// import Switch from 'vue-bulma-switch'
import VbSwitch from 'vue-bulma-switch'
import { Tabs, TabPane } from 'vue-bulma-tabs'
import Tooltip from 'vue-bulma-tooltip'
import Chart from 'vue-bulma-chartjs'

export default {
  components: {
    VbSwitch,
    Tabs,
    TabPane,
    Tooltip,
    Chart
  },

  data () {
    return {
      value: false,
      waf: true,
      finger: true,
      app: true,
      ip: '115.29.79.91',
      hostname: 'iZ28bddlgfsZ',
      myip: 'unknow',
      init: 0,
      options: {
        segmentShowStroke: false
      },
      labels: [
        '正常请求',
        '攻击访问',
        '疑似0Day'
      ],
      data: [
        1,
        1,
        1
      ],
      backgroundColor: [
        '#1fc8db',
        '#fce473',
        '#ed6c63',
        '#42afe3',
        '#97cd76'
      ],
      xss: [
        {
          sre: 'image',
          des: 'Pictures'
        }
      ],
      sql: [
        {
          sre: 'image',
          des: 'Pictures'
        }
      ],
      unknow: [
        {
          sre: 'image',
          des: 'Pictures'
        }
      ]
    }
  },

  computed: {
    switchStat () {
      return this.value ? 'On' : 'Off'
    },
    pieData () {
      return {
        labels: this.labels,
        datasets: [{
          data: this.data,
          backgroundColor: this.backgroundColor
        }]
      }
    },
    image2 () {
      return {
        labels: this.labels,
        datasets: [{
          data: this.data,
          backgroundColor: this.backgroundColor
        }]
      }
    },
    wafupdate () {
      console.log(this.$http)
      if (this.waf === false) {
        this.axios.get('/api/?ip=' + this.ip + '&m=closewaf').then(function (response) {
          console.log('WAF 关闭')
        })
        return 'WAF 关闭'
      } else {
        if (this.init !== 0) {
          this.axios.get('/api/?ip=' + this.ip + '&m=openwaf').then(function (response) {
            console.log('WAF 关闭')
          })
        } else {
          this.init = 1
        }
        return 'WAF 开启'
      }
    },
    fingerupdate () {
      console.log(this.$http)
      if (this.finger === false) {
        this.axios.get('/api/?ip=' + this.ip + '&m=closefinger').then(function (response) {
          console.log('指纹 关闭')
        })
        return '指纹 关闭'
      } else {
        this.axios.get('/api/?ip=' + this.ip + '&m=openfinger').then(function (response) {
          console.log('指纹 关闭')
        })
        return '指纹 开启'
      }
    },
    appupdate () {
      console.log(this.$http)
      if (this.app === false) {
        this.axios.get('/api/?ip=' + this.ip + '&m=shutdown').then(function (response) {
          console.log('应用服务 关闭')
        })
        return '应用服务 关闭'
      } else {
        this.axios.get('/api/?ip=' + this.ip + '&m=resue').then(function (response) {
          console.log('应用服务 关闭')
        })
        return '应用服务 开启'
      }
    }
  },

  methods: {
    ok () {
      this.axios.get('/').then(function (response) {
        console.log(response)
      }).catch(function (error) {
        console.log(error)
      })
    },
    log () {
      return 'http://' + this.ip + ':5000/getlog'
    },
    image () {
      var todo = this
      this.axios.get('./api/?ip=' + this.ip + '&m=getdata').then(function (response) {
        console.log(response)
        console.log(todo.data)
        var re = [0, 0, 0]
        for (var e in response.data) {
          if (e === 'normal_count') {
            re[0] = response.data[e]
          } else if (e === 'err_count') {
            re[1] = response.data[e]
          } else {
            re[2] = response.data[e]
          }
        }
        todo.data = re
      })
      return {
        labels: this.labels,
        datasets: [{
          data: this.data,
          backgroundColor: this.backgroundColor
        }]
      }
    },
    info () {
      var todo = this
      this.axios.get('./api/?ip=' + this.ip + '&m=get_err').then(function (response) {
        console.log(response)
        console.log(todo)
        var xss = []
        var sql = []
        var unknow = []
        response.data.forEach((e) => {
          if (e.cls === 'XSS攻击') {
            xss.push({
              sre: e.url,
              des: todo.myip
            })
          } else if (e.cls === 'SQL注入') {
            sql.push({
              sre: e.url,
              des: todo.myip
            })
          } else {
            unknow.push({
              sre: e.url,
              des: todo.myip
            })
          }
        })
        todo.xss = xss
        todo.sql = sql
        todo.unknow = unknow
      })
    },
    upmyip () {
      var todo = this
      this.axios.get('./api/ip.php').then(function (response) {
        todo.myip = response.data
      })
    }
  },
  created () {
    setInterval(() => {
      this.image()
      this.info()
      this.upmyip()
    }, 3000)
  }
}
</script>

<style lang="scss" scoped>
.button {
  margin: 5px 0 0;
}
p {
  margin-bottom: 20px;
}
.tooltip-value {
  width: 100%;
}
</style>



